.label {
  display: inline-block;
  padding: 4px;
  border-radius: 4px;
  font-size: 14px;
  font-weight: 500;
}
.label.low {
  color: #d50040;
  background-color: rgba(244, 4, 76, 0.1);
}
.label.fair {
  color: #da5c00;
  background-color: rgba(255, 77, 0, 0.1);
}
.label.average {
  color: #a97b00;
  background-color: rgba(255, 186, 0, 0.18);
}
.label.good {
  color: #007831;
  background-color: rgba(138, 190, 45, 0.18);
}
.label.great {
  color: #006027;
  background-color: rgba(0, 166, 81, 0.18);
}
